<script>
export let examples = [];

let selectedIndex = 0;

function selectExample(index) {
  selectedIndex = index;
}

$: {
  selectedIndex = 0;
}
</script>

<style type="text/scss">p {
  font-size: 1.2rem;
  font-style: italic;
  font-weight: lighter;
  color: #000;
  text-align: left;
  min-block-size: 5rem; }

button {
  inline-size: 3rem;
  block-size: 3rem;
  border-radius: 50%;
  font-size: 1.3rem;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  margin: 0.5rem;
  cursor: pointer; }
  button.selected {
    color: white;
    background-color: #44a;
    border: none; }
  button:focus {
    outline: none; }
</style>

<div>
  <p>{examples[selectedIndex]}</p>
  {#if examples.length > 1}
    {#each examples as e, i}
      <button
        class:selected={selectedIndex === i}
        on:click={() => selectExample(i)} />
    {/each}
  {/if}
</div>
